ACA,iBAAA,CAHA,UAAA,CAKA,WAAA,CAEA,kBAAA,CANA,YAAA,CAKA,mBAAA,CAFA,UAIA,CACA,yBACA,SAAA,CACA,cACA,CACA,8BACA,YACA,CAEA,8BACA,kBAAA,CAEA,sBAAA,CACA,eAAA,CACA,gBACA,CAEA,+BACA,iBACA,CAEA,0BACA,iBACA,CAEA,6BACA,kBAAA,CACA,UACA,CAEA,mCACA,kBAAA,CACA,UACA,CAMA,iDAHA,cAMA,CAHA,6BACA,qBAEA,CAGA,+BACA,YAAA,CACA,iBAGA,CAEA,gCAGA,WAAA,CAFA,cAAA,CACA,UAEA,CAEA,oBACA,oBACA","sourcesContent":["<template>\n    <div>\n        <!-- <div class=\"contact\">\n            <div class=\"contact-list\">\n                <ul v-for=\"contact in contacts\" :key=\"contact.id\">\n                    <li>\n                        <ul\n                            v-for=\"con in contact\"\n                            v-if=\"con.host_id == hostId\"\n                            :key=\"con.id\"\n                        >\n                            <li>\n                                <b class=\"h5 text-primary\"\n                                    >Contact {{ con.contact }}</b\n                                >\n                                <i class=\"h6 text-secondary\">\n                                    {{ con.name }} | {{ con.mobile }}</i\n                                >\n                            </li>\n                        </ul>\n                    </li>\n                </ul>\n            </div>\n\n            <form @submit.prevent=\"submit\" class=\"contact-form\">\n                <div\n                    class=\"wed-host-section-container-title h1 animate__animated animate__bounce animate__delay-2s animate__repeat-2\"\n                >\n                    Contact Form\n                </div>\n                <div\n                    v-if=\"success\"\n                    class=\"alert alert-success alert-dismissible fade show mt-3\"\n                    role=\"alert\"\n                >\n                    Contact Saved !\n                    <button\n                        type=\"button\"\n                        class=\"close\"\n                        data-dismiss=\"alert\"\n                        aria-label=\"Close\"\n                    >\n                        <span aria-hidden=\"true\">&times;</span>\n                    </button>\n                </div>\n\n                <div class=\"form-group\">\n                    <input\n                        type=\"hidden\"\n                        class=\"form-control\"\n                        name=\"host_id\"\n                        id=\"name\"\n                        v-model=\"fields.host_id\"\n                    />\n\n                    <label for=\"contact\">For</label>\n                    <input\n                        type=\"text\"\n                        class=\"form-control\"\n                        name=\"contact\"\n                        id=\"contact\"\n                        v-model=\"fields.contact\"\n                    />\n                    <div v-if=\"errors && errors.contact\" class=\"text-danger\">\n                        {{ errors.contact[0] }}\n                    </div>\n                </div>\n\n                <div class=\"form-group\">\n                    <label for=\"name\">Name</label>\n                    <input\n                        type=\"text\"\n                        class=\"form-control\"\n                        name=\"name\"\n                        id=\"name\"\n                        v-model=\"fields.name\"\n                    />\n                    <div v-if=\"errors && errors.name\" class=\"text-danger\">\n                        {{ errors.name[0] }}\n                    </div>\n                </div>\n\n                <div class=\"form-group\">\n                    <label for=\"mobile\">Mobile</label>\n                    <input\n                        type=\"text\"\n                        class=\"form-control\"\n                        name=\"mobile\"\n                        id=\"mobile\"\n                        v-model=\"fields.mobile\"\n                    />\n                    <div v-if=\"errors && errors.mobile\" class=\"text-danger\">\n                        {{ errors.mobile[0] }}\n                    </div>\n                </div>\n                <div class=\"form-group text-center mt-5\">\n                    <button type=\"submit\" class=\"btn btn-sm wed-btn-main\">\n                        <i class=\"material-icons\">save</i>\n                    </button>\n                </div>\n            </form>\n        </div> -->\n        <div>\n            <div class=\"wed-host-section-container-title\">Contact Members</div>\n            <div class=\"d-flex justify-content-around my-4\">\n                <button\n                    v-if=\"editing\"\n                    @click=\"doEdit(false)\"\n                    class=\"btn btn-sm btn-cancel\"\n                    title=\"close form\"\n                >\n                    Cancel\n                </button>\n                <button\n                    v-else\n                    @click=\"doEdit(true)\"\n                    class=\"btn btn-sm wed-btn-main\"\n                    title=\"add new Member\"\n                >\n                    <span class=\"material-icons\"> person_add </span>\n                </button>\n            </div>\n            <div class=\"wed-host-section-container-form\" v-if=\"editing == true\">\n                <form @submit.prevent=\"submit\" class=\"contact-form\">\n                    <div class=\"form-group\">\n                        <input\n                            type=\"hidden\"\n                            class=\"form-control\"\n                            name=\"host_id\"\n                            id=\"name\"\n                            v-model=\"fields.host_id\"\n                        />\n\n                        <label for=\"contact\">For</label>\n                        <input\n                            type=\"text\"\n                            class=\"form-control\"\n                            name=\"contact\"\n                            id=\"contact\"\n                            v-model=\"fields.contact\"\n                            required\n                        />\n                        <div\n                            v-if=\"errors && errors.contact\"\n                            class=\"text-danger\"\n                        >\n                            {{ errors.contact[0] }}\n                        </div>\n                    </div>\n\n                    <div class=\"form-group\">\n                        <label for=\"name\">Name</label>\n                        <input\n                            type=\"text\"\n                            class=\"form-control\"\n                            name=\"name\"\n                            id=\"name\"\n                            required\n                            v-model=\"fields.name\"\n                        />\n                        <div v-if=\"errors && errors.name\" class=\"text-danger\">\n                            {{ errors.name[0] }}\n                        </div>\n                    </div>\n\n                    <div class=\"form-group\">\n                        <label for=\"mobile\">Mobile</label>\n                        <input\n                            type=\"text\"\n                            class=\"form-control\"\n                            name=\"mobile\"\n                            id=\"mobile\"\n                            required\n                            v-model=\"fields.mobile\"\n                        />\n                        <div v-if=\"errors && errors.mobile\" class=\"text-danger\">\n                            {{ errors.mobile[0] }}\n                        </div>\n                    </div>\n                    <div class=\"form-group text-center mt-5\">\n                        <button type=\"submit\" class=\"btn btn-sm wed-btn-main\">\n                            <i class=\"material-icons\">save</i>\n                        </button>\n                    </div>\n                </form>\n            </div>\n            <div class=\"table-responsive\" v-if=\"contacts.length > 0\">\n                <table class=\"table table-striped mx-auto w-auto\" id=\"table\">\n                    <thead>\n                        <tr>\n                            <th scope=\"col\">Name</th>\n                            <th scope=\"col\">Contact For</th>\n                            <th scope=\"col\">Mobile</th>\n                            <th scope=\"row\" colpan=\"2\" class=\"text-center\">\n                                Actions\n                            </th>\n                        </tr>\n                    </thead>\n                    <tbody>\n                        <tr v-for=\"member in reversedMembers\" :key=\"member.id\">\n                            <th scope=\"row\">{{ member.name }}</th>\n                            <td>{{ member.contact }}</td>\n                            <td>{{ member.mobile }}</td>\n                            <td\n                                @click=\"\n                                    setVal(\n                                        member.id,\n                                        member.name,\n                                        member.contact,\n                                        member.mobile\n                                    )\n                                \"\n                            >\n                                <span class=\"material-icons text-primary\"\n                                    >edit</span\n                                >\n                            </td>\n                            <td\n                                @click=\"deleteMember(member.id)\"\n                                class=\"text-danger\"\n                            >\n                                <span class=\"material-icons text-danger\"\n                                    >delete</span\n                                >\n                            </td>\n                        </tr>\n                    </tbody>\n                </table>\n            </div>\n            <!-- Modal -->\n            <div v-if=\"showForm\" class=\"overlay-form\">\n                <form\n                    @submit.prevent=\"updateMember($event)\"\n                    class=\"task-form-emp\"\n                >\n                    <div class=\"d-flex justify-content-end\">\n                        <i\n                            @click=\"showForm = false\"\n                            style=\"font-size: 30px; cursor: pointer\"\n                            class=\"fas fa-times-circle\"\n                        ></i>\n                    </div>\n                    <div class=\"form-group\">\n                        <input\n                            type=\"hidden\"\n                            class=\"form-control\"\n                            name=\"host_id\"\n                            id=\"name\"\n                            v-model=\"fields.host_id\"\n                        />\n\n                        <label for=\"contact\">For</label>\n                        <input\n                            type=\"text\"\n                            class=\"form-control\"\n                            name=\"contact\"\n                            id=\"contact\"\n                            v-model=\"fields.contact\"\n                            required\n                        />\n                        <div\n                            v-if=\"errors && errors.contact\"\n                            class=\"text-danger\"\n                        >\n                            {{ errors.contact[0] }}\n                        </div>\n                    </div>\n\n                    <div class=\"form-group\">\n                        <label for=\"name\">Name</label>\n                        <input\n                            type=\"text\"\n                            class=\"form-control\"\n                            name=\"name\"\n                            id=\"name\"\n                            required\n                            v-model=\"fields.name\"\n                        />\n                        <div v-if=\"errors && errors.name\" class=\"text-danger\">\n                            {{ errors.name[0] }}\n                        </div>\n                    </div>\n\n                    <div class=\"form-group\">\n                        <label for=\"mobile\">Mobile</label>\n                        <input\n                            type=\"text\"\n                            class=\"form-control\"\n                            name=\"mobile\"\n                            id=\"mobile\"\n                            required\n                            v-model=\"fields.mobile\"\n                        />\n                        <div v-if=\"errors && errors.mobile\" class=\"text-danger\">\n                            {{ errors.mobile[0] }}\n                        </div>\n                    </div>\n                    <button\n                        type=\"submit\"\n                        class=\"btn btn-primary mb-2 btn-custm\"\n                    >\n                        Save\n                    </button>\n                </form>\n            </div>\n            <div\n                v-if=\"success\"\n                class=\"alert alert-success alert-dismissible fade show mt-3\"\n                role=\"alert\"\n            >\n                {{ message }}\n                <button\n                    type=\"button\"\n                    class=\"close\"\n                    data-dismiss=\"alert\"\n                    aria-label=\"Close\"\n                >\n                    <span aria-hidden=\"true\">&times;</span>\n                </button>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nexport default {\n    props: [\"hostId\", \"host\", \"invitation\"],\n    data() {\n        return {\n            fields: {\n                host_id: this.hostId,\n                contact: null,\n                name: null,\n                mobile: null,\n            },\n            message: null,\n            updateID: null,\n            errors: {},\n            success: false,\n            loaded: true,\n            contacts: [],\n            //\n            editing: false,\n            showForm: false,\n        };\n    },\n    mounted() {\n        this.loadContacts();\n    },\n    methods: {\n        doEdit(editing) {\n            this.editing = editing;\n            this.errors = null;\n            this.fields.contact = null;\n            this.fields.name = null;\n            this.fields.mobile = null;\n            this.fields.contact = null;\n            delete this.fields.slug;\n            delete this.fields.invitation_id;\n        },\n\n        numberRistrict() {\n            document\n                .querySelectorAll('input[type=\"number\"]')\n                .forEach(function (item) {\n                    item.addEventListener(\"keypress\", function (evt) {\n                        if (\n                            (evt.which != 8 &&\n                                evt.which != 0 &&\n                                evt.which < 48) ||\n                            evt.which > 57\n                        ) {\n                            evt.preventDefault();\n                        }\n                    });\n                });\n        },\n        setVal(val_id, val_name, val_for, val_mobile) {\n            this.fields.name = val_name;\n            this.fields.contact = val_for;\n            this.fields.mobile = val_mobile;\n            this.updateID = val_id;\n            this.showForm = true;\n        },\n        //\n        loadContacts() {\n            var _this = this;\n            axios\n                .get(\"/getcontacts\")\n                .then((response) => {\n                    _this.contacts = response.data.message;\n                })\n                .catch((error) => console.log(error));\n            //console.log(this.contacts);\n            // axios({method: 'GET', url: route('hostinvitationcontact.index', route().params)})\n            //     .then( response => {this.contacts = response.data; console.log(this.contacts);})\n            //     .catch( error => console.log(errors));\n        },\n        updateMember() {\n            var _this = this;\n            this.success = false;\n            _this.errors = {};\n            //const slug = this.slugify(this.fields.name);\n            //const host_id = this.hostid;\n            const invitation_id = this.invitation.slug;\n            const id = this.updateID;\n            //var model = document.getElementById(\"myModal\").value;\n            axios\n                .patch(\n                    `/host/${invitation_id}/invitationcontact/${id}`,\n                    _this.fields\n                )\n                .then((response) => {\n                    _this.contacts = response.data.message;\n                    _this.success = true;\n                    _this.fields = {\n                        host_id: this.hostId,\n                        contact: null,\n                        name: null,\n                        mobile: null,\n                    }; //Clear input fields.\n                    _this.showForm = false;\n                    this.message = \"updated!\";\n                    _this.errors = {};\n                })\n                .catch((error) => {\n                    _this.loaded = true;\n                    if (error.response.status === 422) {\n                        _this.errors = error.response.data.errors || {};\n                    }\n                });\n        },\n        deleteMember(e) {\n            var _this = this;\n            this.success = false;\n            _this.errors = {};\n            const id = e;\n            const invitation_id = this.invitation.slug;\n            axios\n                .delete(`/host/${invitation_id}/invitationcontact/${id}`)\n                .then((response) => {\n                    console.log(response.data);\n                    _this.contacts = response.data.message;\n                    _this.success = true;\n                    this.message = \"deleted!\";\n                    _this.errors = {};\n                })\n                .catch((error) => {\n                    _this.loaded = true;\n                    if (error.response.status === 422) {\n                        _this.errors = error.response.data.errors || {};\n                    }\n                });\n        },\n        submit() {\n            let _this = this;\n            if (this.loaded) {\n                this.loaded = false;\n                this.success = false;\n                this.errors = {};\n                //const  newfields = {invitation_id : this.invitation.id, slug: 'rajesh'};\n                //const slug = this.slugify(this.fields.name);\n                //const host_id = this.hostid;\n                const invitation_id = this.invitation.id;\n                this.fields = { ...this.fields, invitation_id };\n                //const params = {host: this.host, invitation: this.invitation, slug: this.slug};\n                // console.log(params);\n                axios({\n                    method: \"POST\",\n                    url: route(\"hostinvitationcontact.store\", route().params),\n                    data: this.fields,\n                })\n                    .then((response) => {\n                        console.log(response.data);\n                        this.contacts = response.data.messages;\n                        this.success = true;\n                        _this.fields = {\n                            host_id: this.hostId,\n                            contact: null,\n                            name: null,\n                            mobile: null,\n                        };\n                        this.loaded = true;\n                        this.message = \"Added!\";\n                        _this.editing = false;\n                        _this.errors = {};\n                    })\n                    .catch((error) => {\n                        this.loaded = true;\n                        if (error.response.status === 422) {\n                            this.errors = error.response.data.errors || {};\n                        }\n                    });\n            }\n        },\n        slugify(string) {\n            return string\n                .toString()\n                .trim()\n                .toLowerCase()\n                .replace(/\\s+/g, \"-\")\n                .replace(/[^\\w\\-]+/g, \"\")\n                .replace(/\\-\\-+/g, \"-\")\n                .replace(/^-+/, \"\")\n                .replace(/-+$/, \"\");\n        },\n    },\n    computed: {\n        reversedMembers() {\n            return [...this.contacts].reverse().sort();\n        },\n    },\n};\n</script>\n\n<style scoped>\n/* new css */\n.overlay-form {\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    overflow-y: auto;\n    background-color: rgba(0, 0, 0, 0.4);\n    z-index: 999;\n}\n.task-form-emp {\n    width: 60%;\n    background-color: #fafafa;\n    margin: 12% auto;\n    padding: 3em;\n    border-radius: 15px;\n    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;\n}\n.task-form-emp label {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 600;\n    font-size: 17px;\n    line-height: 49px;\n    color: #000000;\n}\n.cont-around {\n    display: flex;\n    justify-content: space-around;\n}\n.cont-around > div {\n    width: 45%;\n}\n.multiple_cont > div {\n    width: 32%;\n}\n.btn-custm {\n    margin-top: 2em;\n}\n.edit-btn-tab > button {\n    background-color: #461952;\n    color: white;\n    outline: none;\n    border: none;\n    border-radius: 50%;\n    width: 30px;\n    height: 30px;\n    padding: 1px 0px 0px 1px;\n    margin-bottom: 10px;\n}\n.errMsg {\n    color: red;\n    font-size: 10px;\n}\n.full-height {\n    height: 100vh;\n}\n\n.flex-center {\n    align-items: center;\n\n    justify-content: center;\n    margin-left: 20%;\n    margin-right: 20%;\n}\n\n.position-ref {\n    position: relative;\n}\n\n.content {\n    text-align: center;\n}\n\n.btn-cancel {\n    background: #ef5753;\n    color: #000;\n}\n\n.btn-cancel:hover {\n    background: #e3342f;\n    color: rgba(255, 255, 255);\n}\n\ntd {\n    cursor: pointer;\n}\n\ninput:hover {\n    background-color: pink;\n    cursor: pointer;\n}\n\n/* new css end */\n.contact-form {\n    height: 100vh;\n    position: relative;\n    /* top: 8em;\n    left: 3em; */\n}\n\n.alert-success {\n    position: fixed;\n    right: 25px;\n    bottom: 25px;\n}\n\nul {\n    list-style-type: none;\n}\n</style>\n"],"sourceRoot":""}]);
